<template>
    <div style="width:1000px">
        <v-data-table :headers="headers" :items="vacation" class="elevation-4">
            <template v-slot:top>
                <v-toolbar flat color="white">
                    <v-toolbar-title>  </v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" @click="onClickRefreshButton">  </v-btn>
                </v-toolbar>
            </template>
            <template v-slot:item.actions="{ item }">
                <v-btn v-if="item.cancelable" color="warning" small @click="onClickCancelVacation(item)"> </v-btn>
            </template>
            <template v-slot:no-data>
                <div>
                    <b> </b>
                </div>
            </template>
        </v-data-table>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import moment from 'moment'
export default {
    data() {
        return {
            headers: [
                {text: ' ', value: 'actions', divider: true},
                {text: ' ', value: 'id', divider: true},
                {text: ' ', value: 'startDate', divider: true},
                {text: ' ', value: 'endDate', divider: true},
                {text: '  ', value: 'days', divider: true},
                {text: '', value: 'createdAt', divider: true},
                {text: '', value: 'updatedAt', divider: true},
            ],
            list: [],
            id: ''
        }
    },

    watch: {
        'vacation': function(item) {
           item.forEach(element => {
               if(element.startDate) element.startDate = moment(element.startDate).format('YYYY-MM-DD');
               if(element.endDate) element.endDate = moment(element.endDate).format('YYYY-MM-DD');
               if(element.createdAt) element.createdAt = moment(element.createdAt).format('YYYY-MM-DD HH:mm:ss');
               if(element.updatedAt) element.updatedAt = moment(element.updatedAt).format('YYYY-MM-DD HH:mm:ss');
           });
        }
    },

    computed: {
        ...mapState({
            user: 'user',
            vacation: 'vacation'
        }),
    },

    methods: {

        ...mapActions([
            'FETCH_USER',
            'FETCH_VACATION',
            'UPDATE_VACATION'
        ]),

        onClickRefreshButton() {
            this.getVacations();
        },

        async onClickCancelVacation(item) {
            try {
                await this.UPDATE_VACATION({item});
                await this.FETCH_VACATION({userId: item.userId});
                await this.FETCH_USER({userId: item.userId})
            } catch (error) {
                console.log(error);
            }
        },

        async getVacations() {
            try {
                await this.FETCH_VACATION({userId: this.user[0].id});
            } catch (error) {
                console.log(error);
            }
        }
    },

    created() {
        this.getVacations();
    },
}
</script>